.chat{
    flex: 0.7;
    flex-grow: 1;
    overflow-y: scroll;
    padding-bottom: 150px;
}
.chat_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid lightgray;
}
.chat_channelName{
    text-transform: lowercase;
    display: flex;
    align-items: center;
}
.chat_channelName > .MuiSvgIcon-root{
    margin-left: 10px;
    font-size: 18px;
}
.chat_headerRight > p{
    display: flex;
    align-items: center;
    font-size: 14px;
}
.chat_headerRight > p > .MuiSvgIcon-root{
    margin-right: 5px !important;
    font-size: 16px;
}
.emp{
    margin-bottom: 200px;
    position: fixed;
}